
#changeDiscount {
  position: absolute;
  width: 100%;
  min-height: 100%;
  z-index: 101;
  top: 0;
  background-color: #f5f5f5;
}
#changeDiscount #discountWrap {
  margin-top: 98px;
}
#changeDiscount #discountWrap .list {
  position: absolute;
  top: 50px;
  bottom: 10px;
  z-index: -1;
  width: 100%;
/*overflow:hidden*/
}
#changeDiscount #discountWrap .list .canDiscount {
  height: 55px;
  text-align: center;
  background-color: #fff;
}
#changeDiscount #discountWrap .list .canDiscount .butt {
  margin: 10px;
  line-height: 45px;
  background-color: #dab96e;
  color: #fff;
}
#changeDiscount #discountWrap .list .slideBar {
  padding-top: 10px;
}
#changeDiscount #discountWrap .list .slideBar .item {
  margin: 0 10px 10px 10px;
/*height:102px*/
  position: relative;
  padding-left: 117px;
}
#changeDiscount #discountWrap .list .slideBar .item div {
  height: 102px;
}
#changeDiscount #discountWrap .list .slideBar .item .left {
  position: absolute;
  left: 0;
  top: 0;
  background-color: #fff;
  width: 100px;
  text-align: center;
  line-height: 100px;
  border: 1px solid #ddd;
  border-right: none;
  font-family: "";
  font-size: 13px;
  color: #c7a770;
}
#changeDiscount #discountWrap .list .slideBar .item .left span {
  font-size: 26px;
}
#changeDiscount #discountWrap .list .slideBar .item .center {
  position: absolute;
  left: 101px;
  top: 0;
  background: url("../../assets/images/bg_disc2.png") no-repeat;
  background-size: 17px 100px;
  width: 17px;
}
#changeDiscount #discountWrap .list .slideBar .item .right {
  background-color: #fff;
  border: 1px solid #ddd;
  border-left: none;
  padding-left: 15px;
}
#changeDiscount #discountWrap .list .slideBar .item .right h4 {
  margin-top: 15px;
  margin-bottom: 8px;
  font-size: 20px;
  color: #666;
}
#changeDiscount #discountWrap .list .slideBar .item .right p {
  color: #999;
  font-size: 14px;
  line-height: 20px;
}
#changeDiscount #discountWrap .list .dated .list .slideBar .item .left {
  color: #ccc;
}
#changeDiscount #discountWrap .list .dated .list .slideBar .item .left span {
  color: #ccc;
}
#changeDiscount #discountWrap .list .dated .list .slideBar .item .right h4 {
  color: #ccc;
}
#changeDiscount #discountWrap .list .dated .list .slideBar .item .right p {
  color: #ccc;
}


