
#plan .planWrap {
  margin-top: 45px;
  margin-bottom: 80px;
}
#plan .planWrap .CarInfo {
  width: 100%;
  height: 90px;
  background-color: #fff;
  position: relative;
  border-bottom: 1px solid #ddd;
/*margin-bottom:10px*/
}
#plan .planWrap .CarInfo:before {
  width: 30px;
  height: 90px;
  position: absolute;
  right: 0;
  text-align: center;
  line-height: 90px;
  color: #999;
}
#plan .planWrap .CarInfo .CarInfoWrap {
  padding-left: 70px;
  overflow: hidden;
}
#plan .planWrap .CarInfo .CarInfoWrap .carLogo {
  position: absolute;
  top: 15px;
  left: 0;
  width: 40px;
  height: 40px;
  padding: 10px;
  float: left;
}
#plan .planWrap .CarInfo .CarInfoWrap .carLogo img {
  margin: 0 auto;
  max-width: 100%;
}
#plan .planWrap .CarInfo .CarInfoWrap .carInfo {
  float: left;
  width: 100%;
  font-family: "";
  position: relative;
}
#plan .planWrap .CarInfo .CarInfoWrap .carInfo .carID {
  margin: 15px 0;
  width: 105px;
  height: 32px;
  background-image: url("../assets/images/bg_carID.png");
  background-size: 100% 100%;
  font-family: "";
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  line-height: 32px;
  position: relative;
}
#plan .planWrap .CarInfo .CarInfoWrap .carInfo .carID span {
  display: block;
  width: 100px;
  position: absolute;
  top: 0;
  right: -100px;
  color: #999;
  letter-spacing: 0;
}
#plan .planWrap .CarInfo .CarInfoWrap .carInfo .carDesc {
  font-family: "";
  color: #999;
  font-size: 14px;
  height: 16px;
  letter-spacing: 1px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 30px;
}
#plan .planWrap .mileage {
  height: 90px;
  margin-top: 30px;
  position: relative;
}
#plan .planWrap .mileage .mt-range {
  position: absolute;
  width: 68%;
  left: 16%;
  top: -3px;
}
#plan .planWrap .mileage .mt-range .mt-range-thumb {
  width: 50px;
  height: 50px;
  text-align: center;
  background: url("../assets/images/icon_plancar.png") no-repeat;
  background-size: 50px;
  background-position: center;
  margin-top: -13px;
  margin-left: -10px;
  box-shadow: none;
}
#plan .planWrap .mileage .mt-range-runway {
  border-top-style: none;
  border-top-color: rgba(255,255,255,0);
}
#plan .planWrap .mileage .mt-range-progress {
  background: none;
}
#plan .planWrap .mileage .bar {
  height: 21px;
  background: url("../assets/images/icon_rode.png") no-repeat;
  background-size: 100% 100%;
  display: block;
}
#plan .planWrap .mileage .bar div {
/*height: 30px*/
  float: left;
}
#plan .planWrap .mileage .bar .circle {
/*flex:1*/
  display: block;
  float: left;
  width: 33%;
  height: 22px;
  background: url("../assets/images/icon_yuan.png") no-repeat;
  background-size: 16px;
  background-position: center center;
/*&:first-child
                        background-position:right 2px
                    &:last-child
                        background-position:left 2px*/
}
#plan .planWrap .mileage .bar .curCircle {
  width: 50%;
  height: 50px;
  text-align: center;
  background: url("../assets/images/icon_plancar.png") no-repeat;
  background-size: 50px;
  background-position: center;
  margin-top: -15px;
}
#plan .planWrap .mileage .disc {
  margin-top: 20px;
  text-align: center;
  display: block;
}
#plan .planWrap .mileage .disc span {
/*flex:1*/
  display: block;
  float: left;
  width: 33%;
  text-align: center;
/*width:25%*/
}
#plan .planWrap .project {
  background-color: #fff;
}
#plan .planWrap .project .tt {
  padding-left: 2.5%;
  height: 40px;
  line-height: 40px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
#plan .planWrap .project .itemWrap {
  padding-left: 2.5%;
  padding-top: 10px;
  overflow: hidden;
}
#plan .planWrap .project .itemWrap .item {
  width: 30%;
  height: 70px;
  float: left;
  font-size: 15px;
  text-align: center;
  color: #020202;
  border: 1px solid #999;
  margin-right: 2.5%;
  margin-bottom: 6px;
}
#plan .planWrap .project .itemWrap .item span {
  display: inline-block;
  width: 90%;
  margin: 0 auto;
  height: 30px;
  overflow: hidden;
}
#plan .planWrap .project .itemWrap .item img {
  display: block;
  margin: 3px auto;
  width: 28px;
  height: 28px;
}
#plan .planWrap .btnWrap {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  z-index: 2;
}
#plan .planWrap .btnWrap .charge {
  width: 94%;
  margin: 20px 3% 10px 3%;
  background-color: #c7a770;
  height: 44px;
  line-height: 44px;
  color: #fff;
  border: none;
  font-size: 18px;
}


