
#setting .settingWrap {
  margin-top: 55px;
}
#setting .settingWrap .info {
  margin-top: 15px;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#setting .settingWrap .info div {
  height: 45px;
  line-height: 45px;
  margin-left: 10px;
  border-bottom: 1px solid #ddd;
  position: relative;
}
#setting .settingWrap .info div:last-child {
  border-bottom: none;
}
#setting .settingWrap .info div:before {
  position: absolute;
  right: 10px;
  color: #999;
}
#setting .settingWrap .info div label {
  display: inline-block;
  width: 33%;
  color: #999;
}
#setting .settingWrap .info div input {
  display: inline-block;
  width: 60%;
  height: 44px;
}
#setting .settingWrap .info div span {
  text-align: center;
  display: block;
  margin-right: 10px;
  color: #daba70;
}


