.serveRoot .serveCommon {
  margin-top: 66px;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap {
  margin-top: 111px;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list {
  background-color: #fff;
  height: 120px;
  padding-left: 140px;
  position: relative;
  border-bottom: 1px solid #ddd;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list img {
  position: absolute;
  left: 10px;
