  width: 120px;
  height: 90px;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list .info {
  position: relative;
  padding-top: 10px;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list .info .tt {
  height: 36px;
  color: #333;
  font-size: 18px;
  letter-spacing: 1px;
  width: 83%;
  overflow: hidden;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list .info .desc {
  margin-top: 3px;
  color: #999;
  height: 38px;
  line-height: 18px;
  overflow: hidden;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list .info .iconGroup {
  margin-top: 5px;
  font-size: 15px;
  color: #999;
  position: relative;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list .info .iconGroup .door:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../../assets/images/icon_serve_6.png") no-repeat;
  background-size: 100% 100%;
  margin: 0;
  vertical-align: bottom;
  margin-right: 3px;
}
.serveRoot .serveCommon .serveCommonWrap .listWrap .list .info .iconGroup .store:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../../assets/images/icon_serve_8.png") no-repeat;
