  right: 10px;
  top: 10px;
}
.serveRoot .tip {
  text-align: center;
  font-size: 13px;
  color: #323232;
}
.serveRoot .menuList {
/*margin-left:45px*/
  padding-left: 45px;
  margin-bottom: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
  margin-top: 10px;
}
.serveRoot .menuList p {
  position: relative;
  height: 45px;
  border-bottom: 1px solid #ddd;
  line-height: 45px;
}
.serveRoot .menuList p:before {
  position: absolute;
  right: 10px;
  font-size: 14px;
  color: #323232;
}
.serveRoot .menuList p:last-child {
  border: none;
}
.serveRoot .menuList p a {
  color: #323232;
  position: relative;
  display: block;
}
.serveRoot .menuList p a:before {
  position: absolute;
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  left: -45px;
  vertical-align: middle;
  background-size: 100% 100%;
  margin: 13px;
}
.serveRoot .menuList p .phone {
  color: #323232;
  position: relative;
  display: block;
}
.serveRoot .menuList p .phoneShow {
  display: block;
}
.serveRoot .menuList p .phoneShow:before {
  position: absolute;
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  left: -45px;
  vertical-align: middle;
  background-size: 100% 100%;
  margin: 13px;
  background-image: url("../../assets/images/icon_phone.png");
}
.serveRoot .menuList p .phoneShow .phoneNum {
  color: #999;
  float: right;
  margin-right: 30px;
  font-style: normal;
}
.searchWrap {
  background: #fff;
  border-bottom: 1px solid #ddd;
  position: fixed;
  top: 111px;
  width: 100%;
  padding: 5px 0;
  z-index: 111;
}
.searchWrap input {
  background: #eee;
  display: block;
  width: 95%;
  margin: 0 auto;
  height: 30px;
  border-radius: 7px;
  text-align: center;
  color: #999;
}
.searchWrap .searchMask {
  position: absolute;
  width: 100%;
  height: 46px;
  top: -5px;
  left: 0;
  line-height: 46px;
  text-align: center;
}
