
.carTop .CarInfo {
  width: 100%;
  height: 90px;
  background-color: #fff;
  position: relative;
  border-bottom: 1px solid #ddd;
/*margin-bottom:10px*/
}
.carTop .CarInfo:before {
  width: 30px;
  height: 90px;
  position: absolute;
  right: 0;
  text-align: center;
  line-height: 90px;
  color: #999;
}
.carTop .CarInfo .CarInfoWrap {
  padding-left: 70px;
  overflow: hidden;
}
.carTop .CarInfo .CarInfoWrap .carLogo {
  position: absolute;
  top: 15px;
  left: 0;
  width: 40px;
  height: 40px;
  padding: 10px;
  float: left;
}
.carTop .CarInfo .CarInfoWrap .carLogo img {
  margin: 0 auto;
  max-width: 100%;
}
.carTop .CarInfo .CarInfoWrap .carInfo {
  float: left;
  width: 100%;
  font-family: "";
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID {
  margin: 15px 0;
  width: 105px;
  height: 32px;
  background-image: url("../assets/images/bg_carID.png");
  background-size: 100% 100%;
  font-family: "";
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  line-height: 32px;
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID span {
  display: block;
  width: 80px;
  position: absolute;
  top: 0;
  right: -80px;
  color: #999;
  letter-spacing: 0;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carDesc {
  font-family: "";
  color: #999;
  font-size: 14px;
  letter-spacing: 1px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 30px;
}
.numBar span {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #ddd;
  float: left;
  background-color: #f5f5f5;
}
.numBar span:before {
  font-weight: bold;
}
.numBar .init {
  border-left: none;
  border-right: none;
  width: 45px;
}
.numBar .init input {
  border: none;
  width: 41px;
  padding-left: 3px;
  text-align: center;
}
#cart .cartWrap {
  padding-top: 45px;
}
#cart .cartWrap .CarInfo {
  position: fixed;
  top: 45px;
  z-index: 2;
}
#cart .cartWrap .CarInfo .carDesc {
  height: 16px;
}
#cart .cartWrap .scroll {
/*position:absolute*/
  top: 141px;
  bottom: 100px;
  width: 100%;
  overflow: hidden;
  padding-bottom: 110px;
  padding-top: 100px;
}
#cart .cartWrap .content {
  margin-bottom: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#cart .cartWrap .content .addToCart {
  color: #dab96e !important;
}
#cart .cartWrap .content .addToCart i:before {
  color: #dab96e !important;
}
#cart .cartWrap .content .tt {
/*line-height:50px*/
  line-height: 25px;
  padding: 15px 0;
  padding-left: 12px;
  padding-right: 30px;
  color: #666;
  border-bottom: 1px solid #ddd;
  font-size: 18px;
  margin-bottom: -1px;
  position: relative;
  overflow: hidden;
  position: relative;
}
#cart .cartWrap .content .tt .delIcon {
  position: absolute;
  right: 0px;
  top: 10px;
  padding: 5px 10px 10px 10px;
}
#cart .cartWrap .content .tt .desc {
/*position:relative*/
  line-height: 25px;
  color: #999;
  font-size: 15px;
  float: right;
  padding-right: 10px;
}
#cart .cartWrap .content .tt .desc em {
  color: #c7a770;
  font-style: normal;
  font-family: '';
}
#cart .cartWrap .content .tt .desc em:first-child {
  margin-right: 5px;
}
#cart .cartWrap .content .tt i {
  margin-right: 5px;
  display: inline-block;
  padding: 0;
}
#cart .cartWrap .content .tt i:before {
  color: #999;
  font-size: 24px;
  vertical-align: middle;
}
#cart .cartWrap .content .tt .sel:before {
  color: #c7a770;
}
#cart .cartWrap .content .item {
  margin-left: 42px;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
  position: relative;
  min-height: 60px;
}
#cart .cartWrap .content .item:last-child {
  border: none;
}
#cart .cartWrap .content .item p {
  line-height: 58px;
  float: left;
}
#cart .cartWrap .content .item .cost {
  font-family: '';
}
#cart .cartWrap .content .item .ttl {
  width: 58%;
}
#cart .cartWrap .content .item .ttl span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow: hidden;
}
#cart .cartWrap .content .item .ttl span:first-child {
  margin-top: 2px;
  line-height: 30px;
  height: 30px;
}
#cart .cartWrap .content .item .ttl2 {
  width: 75%;
  padding-bottom: 10px;
}
#cart .cartWrap .content .item .ttl2 span {
  display: block;
  height: 18px;
  line-height: 18px;
  font-size: 14px;
  color: #999;
}
#cart .cartWrap .content .item .ttl2 span label {
  display: inline-block;
  float: left;
}
#cart .cartWrap .content .item .ttl2 span label em {
  font-style: normal;
}
#cart .cartWrap .content .item .ttl2 span .descName {
  width: 65%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#cart .cartWrap .content .item .ttl2 span .descPrice {
  width: 30%;
}
#cart .cartWrap .content .item .change {
  float: right;
  color: #c7a770;
  position: absolute;
  top: 0;
  right: 0;
}
#cart .cartWrap .content .item .change span {
  color: #999;
  margin-left: 5px;
}
#cart .cartWrap .content .serveStore {
  line-height: 55px;
  overflow: hidden;
  padding-left: 12px;
  border-top: 1px solid #ddd;
  margin-top: -1px;
  display: table;
  width: 100%;
}
#cart .cartWrap .content .serveStore p {
  float: left;
}
#cart .cartWrap .content .serveStore p .name {
  display: block;
  font-size: 18px;
  margin-top: 2px;
  line-height: 30px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#cart .cartWrap .content .serveStore p .desc {
  font-size: 15px;
  color: #999;
  display: block;
  line-height: 20px;
  height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow: hidden;
}
#cart .cartWrap .content .serveStore .ttl {
  max-width: 270px;
  overflow: hidden;
}
#cart .cartWrap .content .serveStore .ttl .desc {
  line-height: 25px;
  color: #999;
  font-size: 15px;
  padding-right: 10px;
  padding-bottom: 10px;
}
#cart .cartWrap .content .serveStore .ttl .desc em {
  color: #c7a770;
  font-style: normal;
  font-family: '\5FAE\8F6F\96C5\9ED1';
}
#cart .cartWrap .content .serveStore .change {
  float: inherit;
  color: #c7a770;
  display: table-cell;
  vertical-align: middle;
  text-align: right;
  width: 20px;
}
#cart .cartWrap .content .serveStore .change span {
  margin-right: 12px;
  display: inline-block;
  width: 16px;
  margin-left: 5px;
  color: #999;
}
#cart .cartWrap .totalWrap {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 50px;
  left: 0;
  background-color: #f5f5f5;
  border-top: 1px solid #ddd;
}
#cart .cartWrap .totalWrap .left {
  float: left;
  line-height: 50px;
  padding-left: 12px;
  color: #333;
}
#cart .cartWrap .totalWrap .left span {
  font-size: 24px;
  vertical-align: middle;
  color: #999;
  margin-right: 5px;
}
#cart .cartWrap .totalWrap .left .sel {
  color: #c7a770;
}
#cart .cartWrap .totalWrap .right {
  height: 50px;
  float: right;
  position: relative;
  padding-right: 107px;
}
#cart .cartWrap .totalWrap .right .buy {
  position: absolute;
  top: 7px;
  right: 12px;
  display: inline-block;
  width: 90px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background-color: #c7a770;
  color: #fff;
}
#cart .cartWrap .totalWrap .right .hj {
  margin-top: 8px;
}
#cart .cartWrap .totalWrap .right .hj span {
  color: #c7a770;
}
#cart .cartWrap .totalWrap .right .hj p {
  font-size: 14px;
  margin-top: 2px;
}
#cart .cartmask {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  z-index: 100;
  top: 0;
  left: 0;
}


