
#orderDetailComp {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
}
#orderDetailComp .orderDetailWrap {
/*padding-top:45px*/
  padding-bottom: 62px;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}
#orderDetailComp .orderDetailWrap .txtInfo {
  padding: 12px;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
}
#orderDetailComp .orderDetailWrap .txtInfo p {
  line-height: 26px;
  color: #333;
}
#orderDetailComp .orderDetailWrap .txtInfo p .name {
  color: #999;
  display: inline-block;
  width: 80px;
}
#orderDetailComp .orderDetailWrap .txtInfo p .gold {
  color: #daba70;
}
#orderDetailComp .orderDetailWrap .content2 {
  padding-bottom: 80px;
}
#orderDetailComp .orderDetailWrap .content .stt {
  line-height: 50px;
  color: #666;
  padding-left: 12px;
}
#orderDetailComp .orderDetailWrap .content .wrap {
  background-color: #fff;
}
#orderDetailComp .orderDetailWrap .content .wrap .storeInfo {
  height: 65px;
  padding-left: 12px;
  padding-right: 25px;
  margin-right: 8px;
  background: url("../assets/images/icon_daohang.png") no-repeat;
  background-size: 20px 24px;
  background-position: right center;
  border-bottom: 1px solid #ddd;
}
#orderDetailComp .orderDetailWrap .content .wrap .storeInfo p {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#orderDetailComp .orderDetailWrap .content .wrap .storeInfo p:first-child {
  line-height: 30px;
  padding-top: 8px;
  font-size: 18px;
  height: 30px;
}
#orderDetailComp .orderDetailWrap .content .wrap .storeInfo p:last-child {
  line-height: 20px;
  font-size: 16px;
  height: 20px;
  color: #999;
}
#orderDetailComp .orderDetailWrap .content .wrap .time {
  line-height: 55px;
  padding-left: 10px;
  border-bottom: 1px solid #ddd;
  color: #999;
}
#orderDetailComp .orderDetailWrap .content .wrap .time span {
  color: #333;
}
#orderDetailComp .orderDetailWrap .content .wrap .ttl {
  line-height: 50px;
  font-size: 17px;
  padding-left: 12px;
  overflow: hidden;
}
#orderDetailComp .orderDetailWrap .content .wrap .ttl span {
  float: right;
  font-size: 15px;
  color: #999;
}
#orderDetailComp .orderDetailWrap .content .wrap .ttl span em {
  font-style: normal;
  color: #c7a770;
  margin-right: 10px;
  font-family: '';
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item {
  min-height: 60px;
  padding-bottom: 3px;
  margin-left: 45px;
  border-bottom: 1px solid #ddd;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item:last-child {
  border: none;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .cost {
  float: right;
  margin-right: 10px;
  line-height: 60px;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt {
/*float:left*/
  width: 100%;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt .name {
  line-height: 30px;
  margin-top: 4px;
  font-size: 17px;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt .desc {
  font-size: 15px;
  color: #999;
  line-height: 15px;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt2 {
  width: 100%;
  color: #7d7d7d;
  font-size: 14px;
  overflow: hidden;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt2 .desc {
  display: block;
  float: left;
  width: 80%;
}
#orderDetailComp .orderDetailWrap .content .wrap .itemWrap .item .tt2 .cost {
  float: left;
  text-align: center;
  width: 18%;
  line-height: 15px;
  margin-right: 0;
}
#orderDetailComp .orderDetailWrap .content .wrap .other {
  padding: 12px;
  background-color: #fff;
  line-height: 26px;
  border-bottom: 1px solid #ddd;
}
#orderDetailComp .orderDetailWrap .content .wrap .other p em {
  float: right;
  font-style: normal;
  color: #c7a770;
  font-family: '';
}
#orderDetailComp .total {
  line-height: 60px;
  position: fixed;
  width: 98%;
  padding: 0 1%;
  bottom: 0;
  left: 0;
  background-color: #fff;
}
#orderDetailComp .total .btnWrap {
  float: right;
}
#orderDetailComp .total .btnWrap button {
  border: 1px solid #aaa;
  background: none;
  height: 32px;
  width: 65px;
  padding: 0;
  margin-right: 5px;
}
#orderDetailComp .total .btnWrap button:last-child {
  color: #c7a770;
  border-color: #c7a770;
}
#orderDetailComp .total em {
  font-style: normal;
  color: #c7a770;
  font-family: '';
}


