
#balance {
  padding-top: 45px;
}
#balance .balanceWrap {
  position: relative;
}
#balance .balanceWrap .money {
  height: 90px;
  border-bottom: 1px solid #ddd;
  text-align: center;
  line-height: 90px;
  font-size: 14px;
  background-color: #fff;
  color: #666;
}
#balance .balanceWrap .money span {
  position: absolute;
  right: 10px;
}
#balance .balanceWrap .money label {
  color: #dab96e;
  font-size: 18px;
}
#balance .balanceWrap .chargeSel {
  background-color: #fff;
  margin-top: 10px;
}
#balance .balanceWrap .chargeSel ul {
  overflow: hidden;
  padding: 10px 0 10px 10px;
}
#balance .balanceWrap .chargeSel ul li {
  display: block;
  float: left;
  width: 30%;
  margin-right: 2.5%;
  height: 40px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
}
#balance .balanceWrap .chargeSel ul li span {
  display: block;
  text-align: center;
}
#balance .balanceWrap .chargeSel ul li span:first-child {
  padding-top: 4px;
  font-size: 17px;
}
#balance .balanceWrap .chargeSel ul li span:last-child {
  font-size: 14px;
  color: #999;
}
#balance .balanceWrap .chargeSel ul .sr {
  width: 96%;
}
#balance .balanceWrap .chargeSel ul .sr input {
  height: 40px;
  width: 97%;
  text-indent: 8px;
}
#balance .balanceWrap .chargeSel ul .sel {
  border: 2px solid #dab96e;
  margin-left: -2px;
  height: 38px;
  color: #dab96e;
}
#balance .balanceWrap .chargeSel ul .sel span:last-child {
  color: #dab96e;
}
#balance .balanceWrap .activity {
  padding: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#balance .balanceWrap .activity li {
  line-height: 30px;
  width: 100%;
  color: #999;
  font-size: 14px;
}
#balance .balanceWrap .activity li span {
  background-color: #c7a770;
  color: #fff;
  display: inline-block;
  text-align: center;
  width: 16px;
  height: 16px;
  line-height: 16px;
  margin-right: 10px;
}
#balance .balanceWrap .charge {
  width: 94%;
  margin: 30px 3%;
  background-color: #c7a770;
  height: 44px;
  line-height: 44px;
  color: #fff;
  border: none;
  font-size: 18px;
}


