
.carTop .CarInfo {
  width: 100%;
  height: 90px;
  background-color: #fff;
  position: relative;
  border-bottom: 1px solid #ddd;
/*margin-bottom:10px*/
}
.carTop .CarInfo:before {
  width: 30px;
  height: 90px;
  position: absolute;
  right: 0;
  text-align: center;
  line-height: 90px;
  color: #999;
}
.carTop .CarInfo .CarInfoWrap {
  padding-left: 70px;
  overflow: hidden;
}
.carTop .CarInfo .CarInfoWrap .carLogo {
  position: absolute;
  top: 15px;
  left: 0;
  width: 40px;
  height: 40px;
  padding: 10px;
  float: left;
}
.carTop .CarInfo .CarInfoWrap .carLogo img {
  margin: 0 auto;
  max-width: 100%;
}
.carTop .CarInfo .CarInfoWrap .carInfo {
  float: left;
  width: 100%;
  font-family: "";
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID {
  margin: 15px 0;
  width: 105px;
  height: 32px;
  background-image: url("../assets/images/bg_carID.png");
  background-size: 100% 100%;
  font-family: "";
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  line-height: 32px;
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID span {
  display: block;
  width: 80px;
  position: absolute;
  top: 0;
  right: -80px;
  color: #999;
  letter-spacing: 0;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carDesc {
  font-family: "";
  color: #999;
  font-size: 14px;
  letter-spacing: 1px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 30px;
}
.numBar span {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #ddd;
  float: left;
  background-color: #f5f5f5;
}
.numBar span:before {
  font-weight: bold;
}
.numBar .init {
  border-left: none;
  border-right: none;
  width: 45px;
}
.numBar .init input {
  border: none;
  width: 41px;
  padding-left: 3px;
  text-align: center;
}
#setBX {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1002;
  background-color: #f5f5f5;
}
#setBX .setBXWrap {
  padding-top: 55px;
}
#setBX .setBXWrap .info {
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#setBX .setBXWrap .info .iconfont {
  height: 45px;
  line-height: 45px;
  position: relative;
  padding: 0 10px;
}
#setBX .setBXWrap .info .iconfont:before {
  position: absolute;
  right: 10px;
  color: #999;
}
#setBX .setBXWrap .info .iconfont label {
  display: inline-block;
  width: 33%;
  color: #999;
}
#setBX .setBXWrap .info .iconfont input {
  display: inline-block;
  width: 60%;
  height: 44px;
  text-align: right;
}


