
#aboutUs .settingWrap {
  margin-top: 75px;
}
#aboutUs .settingWrap .logo {
  margin: 30px 0;
}
#aboutUs .settingWrap .logo img {
  width: 40%;
  display: block;
  margin: 0 auto;
  border-radius: 20px;
}
#aboutUs .settingWrap .content {
  padding: 10px;
  border: 1px solid #ddd;
  background-color: #fff;
  width: 80%;
  margin: 0 auto;
}
#aboutUs .settingWrap .exp {
  position: fixed;
  bottom: 10px;
  width: 100%;
  max-width: 750px;
}
#aboutUs .settingWrap .exp p {
  text-align: center;
  font-size: 13px;
  margin: 3px 0;
  color: #aaa;
}


