
#pattern {
  position: absolute;
  width: 100%;
  z-index: 1002;
  top: 0;
  left: 0;
  min-height: 100%;
  background-color: #fff;
}
#pattern .patternWrap {
  margin-top: 45px;
}
#pattern .patternWrap .list {
  background-color: #fff;
}
#pattern .patternWrap .list:last-child {
  border-bottom: 1px solid #ddd;
}
#pattern .patternWrap .list p {
  background-color: #f5f5f5;
  border-bottom: 1px solid #ccc;
  border-top: 1px solid #ccc;
  line-height: 35px;
  padding-left: 10px;
}
#pattern .patternWrap .list ul li {
  display: block;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #ccc;
  background: #fff;
  font-size: 18px;
  margin-left: 10px;
  overflow: hidden;
}
#pattern .patternWrap .list ul li:last-child {
  border: none;
}


