
#comment .commentWrap {
  padding-top: 55px;
}
#comment .commentWrap .init {
  display: block;
  width: 98%;
  border: none;
  height: 200px;
  padding: 5px 1%;
}
#comment .commentWrap .grade {
  margin-top: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#comment .commentWrap .grade .list {
  overflow: hidden;
  line-height: 50px;
  margin-left: 10px;
  color: #666;
  border-bottom: 1px solid #ddd;
}
#comment .commentWrap .grade .list:last-child {
  border: none;
}
#comment .commentWrap .grade .list .gradeNode {
  float: right;
  padding-right: 10px;
  font-size: 20px;
  color: #999;
}
#comment .commentWrap .grade .list .sel {
  color: #c7a770;
}
#comment .btnWrap {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  z-index: 2;
}
#comment .btnWrap button {
  width: 94%;
  margin: 20px 3% 10px 3%;
  background-color: #c7a770;
  height: 44px;
  line-height: 44px;
  color: #fff;
  border: none;
  font-size: 18px;
}


