
.message {
  position: absolute;
  width: 100%;
  height: 100%;
}
.message .content {
  position: absolute;
  top: 45px;
  width: 100%;
  bottom: 0;
  padding: 10px 0;
  background-color: #eee;
}
.message .content .overflow {
  overflow: hidden;
}
.message .content .scrollBar {
  height: 100%;
}
.message .content .scrollBar .list {
  border-radius: 10px;
  background-color: #fff;
  height: 135px;
  width: 90%;
  margin: 5px auto 10px auto;
}
.message .content .scrollBar .list p {
  padding: 0 0 0 5%;
}
.message .content .scrollBar .list .tt {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #000;
  overflow: hidden;
  position: relative;
}
.message .content .scrollBar .list .tt:before {
  position: absolute;
  right: 20px;
  font-size: 28px;
  top: 6px;
  color: #dab96e;
}
.message .content .scrollBar .list .tm {
  font-size: 15px;
  color: #999;
}
.message .content .scrollBar .list .desc {
  border-top: 1px solid #ddd;
  padding-left: 0;
  padding-top: 7px;
  margin: 10px 5% 0 5%;
  height: 50px;
  line-height: 25px;
  color: #999;
  overflow: hidden;
}


