
#paymentSUC {
  height: 273px;
  background-color: #fff;
}
#paymentSUC .wrap {
  padding-top: 55px;
  text-align: center;
}
#paymentSUC .wrap .iconfont {
  margin-top: 50px;
  display: block;
  height: 80px;
  text-align: center;
  margin-bottom: 10px;
}
#paymentSUC .wrap .iconfont:before {
  font-size: 80px;
  color: #dab96e;
}
#paymentSUC .operateWrap button {
  width: 42.5%;
  height: 35px;
  font-size: 16px;
  display: block;
  margin-top: 20px;
}
#paymentSUC .operateWrap button:first-child {
  background-color: #dab96e;
  color: #fff;
  border: 1px solid #dab96e;
  margin-left: 5%;
  float: left;
}
#paymentSUC .operateWrap button:last-child {
  float: right;
  margin-right: 5%;
  border: 1px solid #ccc;
  background-color: #fff;
}


