
.mint-msgbox {
  top: 180px;
}
.mint-msgbox input {
  height: 30px;
}
#order .orderWrap {
  padding-top: 45px;
  padding-bottom: 100px;
}
#order .orderWrap .mint-navbar {
  border-bottom: 1px solid #ddd;
}
#order .orderWrap .mint-navbar .mint-tab-item-label {
  font-size: 16px;
  color: #666;
}
#order .orderWrap .mint-tab-item.is-selected {
  border-color: #c7a770;
}
#order .orderWrap .mint-tab-item.is-selected .mint-tab-item-label {
  color: #c7a770;
}
#order .orderWrap .list .slideBar .orderItem {
  background-color: #fff;
  margin-top: 10px;
}
#order .orderWrap .list .slideBar .orderItem div {
  padding-left: 10px;
  border-bottom: 1px solid #ddd;
}
#order .orderWrap .list .slideBar .orderItem .status {
  line-height: 50px;
  color: #999;
  font-size: 15px;
  position: relative;
}
#order .orderWrap .list .slideBar .orderItem .status .more {
  position: absolute;
  right: 1px;
  color: #333;
}
#order .orderWrap .list .slideBar .orderItem .status span {
  color: #c7a770;
  font-size: 14px;
}
#order .orderWrap .list .slideBar .orderItem .status .orderStatus {
  float: right;
  margin-right: 15px;
}
#order .orderWrap .list .slideBar .orderItem .ttl {
  line-height: 25px;
  padding: 15px 0;
  overflow: hidden;
  font-size: 17px;
}
#order .orderWrap .list .slideBar .orderItem .ttl span {
  float: right;
  font-size: 15px;
  color: #999;
}
#order .orderWrap .list .slideBar .orderItem .ttl span em {
  font-style: normal;
  color: #c7a770;
  margin-right: 10px;
  font-family: '';
}
#order .orderWrap .list .slideBar .orderItem .itemWrap {
  border-bottom: none;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item {
  min-height: 50px;
/*margin-left:25px*/
  padding-bottom: 5px;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item div {
  padding-bottom: 3px;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item div:last-child {
  border-bottom: none;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item:last-child {
  border: none;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item .tt {
/*float:left*/
  width: 100%;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item .tt span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item .tt .name {
  line-height: 30px;
  margin-top: 4px;
  font-size: 17px;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item .tt2 {
  font-size: 14px;
  color: #7d7d7d;
  overflow: hidden;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item .tt2 .cost {
  width: 18%;
  float: left;
  text-align: center;
}
#order .orderWrap .list .slideBar .orderItem .itemWrap .item .tt2 .desc {
  display: block;
  float: left;
  width: 80%;
  height: 14px;
  line-height: 14px;
  overflow: hidden;
}
#order .orderWrap .list .slideBar .orderItem .hz {
  line-height: 50px;
  text-align: right;
  padding-right: 10px;
  color: #999;
  font-size: 15px;
}
#order .orderWrap .list .slideBar .orderItem .hz em {
  font-style: normal;
  color: #c7a770;
  font-family: '';
}
#order .orderWrap .list .slideBar .orderItem .total {
  line-height: 50px;
}
#order .orderWrap .list .slideBar .orderItem .total .btnWrap {
  float: right;
}
#order .orderWrap .list .slideBar .orderItem .total .btnWrap button {
  border: 1px solid #aaa;
  background: none;
  height: 32px;
  width: 65px;
  padding: 0;
  margin-right: 5px;
}
#order .orderWrap .list .slideBar .orderItem .total .btnWrap button:last-child {
  color: #c7a770;
  border-color: #c7a770;
}
#order .orderWrap .list .slideBar .orderItem .total em {
  font-style: normal;
  color: #c7a770;
  font-family: '';
}


