
@import '../../assets/font/iconfont.css';
.headerMine {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 45px;
  background-color: rgba(0,0,0,0);
  color: #fff;
  line-height: 45px;
}
.headerMine .title {
  width: 100%;
  text-align: center;
  font-size: 20px;
}
.headerMine .headRight {
  position: absolute;
  right: 0;
  top: 0;
  min-width: 80px;
}
.headerMine .headRight span {
  display: inline-block;
  width: 35px;
  font-size: 22px;
}
.headerMine .headRight .search {
  text-align: right;
}
.headerMine .headRight .msgTip {
  text-align: center;
}


