
.advertisement {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
}
.advertisement .header {
  position: absolute;
  z-index: 10;
  top: 8px;
  left: 5px;
  width: 30px;
  height: 30px;
  margin: 0;
  text-align: center;
  line-height: 30px;
  border-radius: 100%;
  background-color: rgba(0,0,0,0.2);
  color: #fff;
}
.advertisement iframe {
  position: absolute;
  z-index: 8;
  left: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
}
.advertisement .headerCommon {
  display: none;
}
.advertisement .messageDesc {
  margin-top: 0px;
}


