/*bg-img($url)
    background-image: url($url+".png")*/
.foot {
  position: fixed;
  bottom: 0;
  left: 0;
/*display: flex*/
  width: 100%;
  height: 50px;
  z-index: 99;
}
.foot .foot-item {
  display: block;
  width: 25%;
  height: 50px;
  float: left;
/*flex:1*/
  text-align: center;
  background: #353535;
  color: #9a9a9a;
  font-size: 15px;
}
.foot .foot-item:before {
  content: '';
  display: block;
  width: 22px;
  height: 22px;
  margin: 6px auto 2px auto;
  background-size: 100% 100%;
}
.foot .router-link-active {
  color: #dab96e;
}
.foot .home:before {
  background-image: url("../../assets/images/icon_home.png");
}
.foot .cart:before {
  background-image: url("../../assets/images/icon_cart.png");
}
.foot .mine:before {
  background-image: url("../../assets/images/icon_mine.png");
}
.foot .order:before {
  width: 20px;
  height: 22px;
  background-image: url("../../assets/images/icon_order.png");
}
.foot .home.router-link-active:before {
  background-image: url("../../assets/images/icon_home_sel.png");
}
.foot .cart.router-link-active:before {
  background-image: url("../../assets/images/icon_cart_sel.png");
}
.foot .mine.router-link-active:before {
  background-image: url("../../assets/images/icon_mine_sel.png");
}
.foot .order.router-link-active:before {
  background-image: url("../../assets/images/icon_order_sel.png");
}


