
#addAddress #addressWrap {
/*margin-top:40px
        margin-bottom:77px*/
  position: absolute;
  width: 100%;
  min-height: 100%;
}
#addAddress #addressWrap .location {
  background-color: #fff;
}
#addAddress #addressWrap .location p {
  line-height: 40px;
  margin-left: 15px;
}
#addAddress #addressWrap .location p:first-child {
  border-bottom: 1px solid #ddd;
}
#addAddress #addressWrap .location p:last-child {
  text-align: right;
}
#addAddress #addressWrap .location p button {
  background-color: #fff;
  padding: 5px 20px;
}
#addAddress #addressWrap .location p button:first-child {
  border: 1px solid #999;
  color: #999;
}
#addAddress #addressWrap .location p button:last-child {
  margin-left: 10px;
  margin-right: 10px;
  border: 1px solid #dab96e;
  color: #dab96e;
}
#addAddress #addressWrap .info {
  margin-top: 15px;
  width: 100%;
/*border-bottom:1px solid #ddd*/
  min-height: 380px;
}
#addAddress #addressWrap .info .addAddressForm {
  background-color: #fff;
}
#addAddress #addressWrap .info .addAddressForm div {
  height: 45px;
  line-height: 45px;
  margin-left: 10px;
  border-bottom: 1px solid #ddd;
  position: relative;
}
#addAddress #addressWrap .info .addAddressForm div:last-child {
  border-bottom: none;
}
#addAddress #addressWrap .info .addAddressForm div:before {
  position: absolute;
  right: 10px;
  color: #999;
}
#addAddress #addressWrap .info .addAddressForm div label {
  display: inline-block;
  width: 33%;
  color: #999;
}
#addAddress #addressWrap .info .addAddressForm div input {
  display: inline-block;
  width: 60%;
  height: 40px;
  font-size: 14px;
}
#addAddress #addressWrap .info .addAddressForm .addressDetail {
  margin-left: 0;
  padding-left: 10px;
}
#addAddress #addressWrap .info .addAddressForm .addressDetail input {
  height: 44px;
  width: 63%;
}
#addAddress #addressWrap .info .defaultSel {
  margin-top: 10px;
  background-color: #fff;
  line-height: 44px;
  padding-left: 10px;
  color: #666;
  border-bottom: 1px solid #ddd;
}
#addAddress #addressWrap .info .defaultSel span {
  margin-right: 8px;
  color: #c3c3c3;
  display: inline-block;
  height: 24px;
  line-height: 24px;
}
#addAddress #addressWrap .info .defaultSel span:before {
  vertical-align: middle;
  font-size: 24px;
  color: #c3c3c3;
}
#addAddress #addressWrap .info .defaultSel .sel:before {
  color: #dab96e;
}
#addAddress #addressWrap .btnWrap {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1;
}
#addAddress #addressWrap .btnWrap .charge {
  width: 94%;
  margin: 20px 3% 10px 3%;
  background-color: #c7a770;
  height: 44px;
  line-height: 44px;
  color: #fff;
  border: none;
  font-size: 18px;
}
#addAddress .selAddWrap {
  position: absolute;
  width: 96%;
  left: 0;
  top: 0px;
  bottom: 0px;
  z-index: 99;
  background-color: #fff;
  padding: 0 2% 10px 2%;
}
#addAddress .selAddWrap .selHeader {
  height: 45px;
  line-height: 45px;
}
#addAddress .selAddWrap .selHeader a {
  float: left;
}
#addAddress .selAddWrap .selHeader .title {
  text-align: center;
}
#addAddress .selAddWrap .scrollBar {
  position: absolute;
  width: 96%;
  top: 45px;
  bottom: 0;
  overflow: hidden;
}
#addAddress .selAddWrap .scrollBar ul {
  display: block;
  min-height: 100%;
  border-top: 1px solid #ddd;
  background-color: #fff;
}
#addAddress .selAddWrap .scrollBar ul li {
  display: block;
  line-height: 40px;
  border-bottom: 1px solid #ddd;
}
#addAddress .roadWrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
/*height:100%*/
  background-color: #fff;
  z-index: 1001;
}
#addAddress .roadWrap .roadback {
  height: 45px;
  line-height: 45px;
  text-align: center;
  border-bottom: #ddd;
}
#addAddress .roadWrap .roadback .back {
  margin-left: 2%;
  float: left;
}
#addAddress .roadWrap .roadListWrap {
  margin: 0 2%;
  border-bottom: 1px solid #ddd;
}
#addAddress .roadWrap .roadListWrap li {
  height: 45px;
  line-height: 45px;
  border-top: 1px solid #ddd;
}
#addAddress .roadWrap .roadListWrap li span {
  float: right;
  color: #333;
}
.mint-indicator-wrapper {
  z-index: 100;
}


