
.agreement {
  position: fixed;
  top: 0;
  left: 0;
  padding: 10px 2%;
  background-color: #fff;
  height: 100%;
  width: 96%;
  overflow-y: auto;
  z-index: 101;
}
.agreement .tt {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #000;
  overflow: hidden;
}
.agreement .tm {
  font-size: 15px;
  color: #999;
}
.agreement .desc2 {
  margin-top: 20px;
  padding-left: 0;
/*height:50px*/
  line-height: 25px;
  color: #999;
  overflow: hidden;
}
.agreement .desc2 p {
  text-indent: 2rem;
}


