
#appointment {
  position: fixed;
  width: 100%;
  min-height: 100%;
  z-index: 101;
  top: 0;
  background-color: #f5f5f5;
}
#appointment .appointmentWrap {
  padding-top: 45px;
}
#appointment .appointmentWrap .dayList {
  position: fixed;
  width: 100%;
  top: 45px;
  left: 0;
  z-index: 2;
  background-color: #fff;
}
#appointment .appointmentWrap .dayList span {
  position: absolute;
  width: 30px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  color: #999;
  font-size: 14px;
  z-index: 5;
}
#appointment .appointmentWrap .dayList .btnLeft {
  left: 0;
}
#appointment .appointmentWrap .dayList .btnRight {
  right: 0;
}
#appointment .appointmentWrap .dayList .swiper-container {
  border-bottom: 1px solid #ddd;
}
#appointment .appointmentWrap .dayList .swiper-container .swiper-button-prev {
  background: none;
  line-height: 45px;
  text-align: center;
}
#appointment .appointmentWrap .dayList .swiper-container .swiper-button-next {
  background: none;
  line-height: 45px;
  text-align: center;
}
#appointment .appointmentWrap .dayList .swiper-container .swiper-wrapper .swiper-slide {
  height: 45px;
  background-color: #fff;
  padding-top: 10px;
  display: block;
  float: left;
  width: 33.33%;
  text-align: center;
  font-size: 17px;
}
#appointment .appointmentWrap .dayList .swiper-container .swiper-wrapper .swiper-slide-active {
  color: #dab96e;
}
#appointment .appointmentWrap .timeList {
  margin-top: 10px;
  padding-left: 4%;
  position: absolute;
  width: 96%;
  top: 100px;
  bottom: 0;
}
#appointment .appointmentWrap .timeList ul {
  overflow: hidden;
}
#appointment .appointmentWrap .timeList ul li {
  background-color: #fff;
  display: block;
  float: left;
  width: 21.5%;
  height: 55px;
  text-align: center;
  line-height: 55px;
  margin-right: 3%;
  margin-bottom: 10px;
}
#appointment .appointmentWrap .timeList ul .dis {
  background-color: #ddd;
}
#appointment .appointmentWrap .timeList ul .sel {
  background-color: #dab96e;
  color: #fff;
}


