
#serveStore .serveStoreWrap {
  padding-top: 45px;
}
#serveStore .serveStoreWrap .btnWrap {
/*height:80px*/
  position: fixed;
  z-index: 2;
  top: 45px;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#serveStore .serveStoreWrap .btnWrap button {
  width: 94%;
  margin: 10px 3% 10px 3%;
  background-color: #c7a770;
  height: 44px;
  line-height: 44px;
  color: #fff;
  border: none;
  font-size: 18px;
}
#serveStore .serveStoreWrap .storeList {
  margin-top: 65px;
}
#serveStore .serveStoreWrap .storeList .tip {
  background-color: #f1f1f1;
  line-height: 32px;
  padding-left: 10px;
}
#serveStore .serveStoreWrap .storeList .list {
  padding-left: 115px;
  padding-top: 10px;
  position: relative;
  background-color: #fff;
  height: 85px;
  border-bottom: 1px solid #ddd;
}
#serveStore .serveStoreWrap .storeList .list .showImg {
  width: 100px;
  height: 75px;
  position: absolute;
  left: 10px;
  top: 10px;
}
#serveStore .serveStoreWrap .storeList .list p:first-child {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333;
  margin-bottom: 10px;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) {
  width: 100%;
  font-size: 14px;
  color: #999;
  position: relative;
  margin-bottom: 10px;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .grade {
  display: inline-block;
  height: 13px;
  line-height: 13px;
  vertical-align: top;
  letter-spacing: -1px;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .grade i {
  font-size: 14px;
  color: #ccc;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .grade .sel {
  color: #ffb400;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .klm {
  position: absolute;
  right: 5px;
}
#serveStore .serveStoreWrap .storeList .list p:last-child {
  color: #999;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (max-width: 374px) {
#serveStore .serveStoreWrap .storeList .tip {
    padding-left: 10px;
}
#serveStore .serveStoreWrap .storeList .list {
    padding-left: 100px;
    padding-top: 10px;
    position: relative;
    background-color: #fff;
    height: 78px;
    border-bottom: 1px solid #ddd;
}
#serveStore .serveStoreWrap .storeList .list .showImg {
    width: 90px;
    height: 67.5px;
    position: absolute;
    left: 5px;
    top: 10px;
}
#serveStore .serveStoreWrap .storeList .list p:first-child {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #333;
    margin-bottom: 10px;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) {
    width: 100%;
    font-size: 13px;
    color: #999;
    position: relative;
    margin-bottom: 10px;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .grade {
    display: inline-block;
    height: 13px;
    line-height: 13px;
    vertical-align: top;
    letter-spacing: -2px;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .grade i {
    font-size: 12px;
    color: #ccc;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .grade .sel {
    color: #ffb400;
}
#serveStore .serveStoreWrap .storeList .list p:nth-child(2) .klm {
    position: absolute;
    right: 5px;
}
#serveStore .serveStoreWrap .storeList .list p:last-child {
    color: #999;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
}


