
#issure {
  margin-bottom: 100px;
}
#issure #issureWrap {
  margin-top: 45px;
}
#issure #issureWrap .tip {
  height: 45px;
  line-height: 45px;
  padding-left: 10px;
  position: fixed;
  z-index: 1;
  top: 45px;
  left: 0;
  width: 100%;
  background-color: #f5f5f5;
}
#issure #issureWrap .slide {
  width: 100%;
  padding-top: 45px;
/*position:absolute
            top:90px
            bottom:70px*/
/*overflow:hidden*/
}
#issure #issureWrap .slide .slideBar .list {
  display: block;
  background-color: #fff;
  padding-left: 10px;
  padding-right: 25px;
/*height:44px*/
  line-height: 44px;
  border-bottom: 1px solid #ddd;
  color: #333;
  position: relative;
  margin-bottom: 10px;
/*overflow:hidden*/
}
#issure #issureWrap .slide .slideBar .list .iconfont {
  position: absolute;
  color: #999;
  right: 8px;
}
#issure #issureWrap .slide .slideBar .list .content {
  display: block;
  height: 44px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#issure #issureWrap .slide .slideBar .list .line {
  height: 1px;
  background-color: #ddd;
  position: absolute;
  top: 44px;
  left: 0;
  width: 100%;
}
#issure #issureWrap .slide .slideBar .list .issureContent {
  display: block;
  line-height: 20px;
  padding-bottom: 15px;
  padding-top: 15px;
  text-indent: 2rem;
  word-break: break-all;
  word-wrap: break-word;
}
#issure #issureWrap .slide .slideBar .list .show {
  display: block;
}
#issure #issureWrap .call {
  position: fixed;
  bottom: 0;
  height: 44px;
  line-height: 44px;
  width: 100%;
  padding: 10px 3% 10px 3%;
  text-align: center;
  background-color: #fff;
}
#issure #issureWrap .call a {
  background-color: #dab96e;
  display: block;
  width: 94%;
  color: #fff;
  font-size: 18px;
  font-family: '';
}
#issure #issureWrap .call a span {
  font-size: 20px;
  padding-right: 10px;
}


