
.messageDesc {
  margin-top: 45px;
  padding: 1%;
  position: absolute;
  top: 0;
  left: 0;
  width: 98%;
  min-height: 100%;
  background-color: #fff;
}
.messageDesc .tt {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #000;
  overflow: hidden;
}
.messageDesc .tm {
  font-size: 15px;
  color: #999;
}
.messageDesc .desc {
  margin-top: 20px;
  padding-left: 0;
/*height:50px*/
  line-height: 25px;
  color: #999;
  overflow: hidden;
  padding-bottom: 60px;
}
.messageDesc .desc p {
  text-indent: 2rem;
}


