
#mine {
  margin-bottom: 60px;
  padding-bottom: 80px;
}
#mine .mine-top {
  position: relative;
  height: 220px;
}
#mine .mine-top .hdbd {
  width: 100%;
  height: 100%;
}
#mine .mine-top .icon-chevron-copy-copy-copy-copy-copy-copy:before {
  position: absolute;
  right: 10px;
  color: #fff;
  top: 35px;
}
#mine .mine-top .info {
  position: absolute;
  height: 80px;
  width: 100%;
  top: 60px;
  left: 0;
}
#mine .mine-top .info .headerImg {
  height: 80px;
  width: 80px;
  border-radius: 100%;
  margin: 0 10px;
  float: left;
}
#mine .mine-top .info .desc {
  margin-top: 10px;
  color: #fff;
  float: left;
  width: 60%;
}
#mine .mine-top .info .desc .userName {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 18px;
  line-height: 25px;
  height: 25px;
}
#mine .mine-top .info .desc .grade {
  margin-top: 10px;
}
#mine .mine-top .info .desc .grade img {
  width: 24px;
  height: 24px;
  margin-right: 5px;
}
#mine .mine-top .account {
  position: absolute;
  bottom: 0;
  height: 65px;
  width: 100%;
  background-color: rgba(0,0,0,0.1);
}
#mine .mine-top .account div {
  float: left;
  width: 50%;
  text-align: center;
  color: #dcdcdc;
  font-size: 17px;
}
#mine .mine-top .account div span {
  display: block;
  line-height: 18px;
  text-align: center;
}
#mine .mine-top .account div .num {
  color: #fff;
}
#mine .mine-top .account div .in {
  margin-top: 8px;
  line-height: 28px;
}
#mine .mine-top .account div .in:before {
  display: inline-block;
  content: '';
  width: 19px;
  height: 13px;
  margin-right: 5px;
  background-image: url("../../assets/images/icon_money.png");
  background-size: 100% 100%;
}
#mine .mine-top .account .left {
  position: relative;
}
#mine .mine-top .account .left:before {
  content: '';
  display: block;
  width: 1px;
  height: 40px;
  background-color: #a5a5a5;
  position: absolute;
  right: 0;
  top: 12px;
}
#mine .mine-top .account .right .in {
  line-height: 28px;
}
#mine .mine-top .account .right .in:before {
  background-image: url("../../assets/images/icon_ticket.png");
}
#mine .tip {
  text-align: center;
  font-size: 13px;
  color: #323232;
}
#mine .menuList {
/*margin-left:45px*/
  padding-left: 45px;
  margin-bottom: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
}
#mine .menuList p {
  position: relative;
  height: 45px;
  border-bottom: 1px solid #ddd;
  line-height: 45px;
}
#mine .menuList p:before {
  position: absolute;
  right: 10px;
  font-size: 14px;
  color: #323232;
}
#mine .menuList p:last-child {
  border: none;
}
#mine .menuList p a {
  color: #323232;
  position: relative;
  display: block;
}
#mine .menuList p a:before {
  position: absolute;
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  left: -45px;
  vertical-align: middle;
  background-size: 100% 100%;
  margin: 13px;
}
#mine .menuList p a label {
  padding-left: 30px;
  color: #999;
}
#mine .menuList .car a:before {
  background-image: url("../../assets/images/icon_car.png");
}
#mine .menuList .post a:before {
  background-image: url("../../assets/images/icon_list.png");
}
#mine .menuList .address a:before {
  background-image: url("../../assets/images/icon_posi.png");
}
#mine .menuList .issue a:before {
  background-image: url("../../assets/images/icon_yiwen.png");
}
#mine .menuList .share a:before {
  background-image: url("../../assets/images/icon_share.png");
}
#mine .menuList .idea a:before {
  background-image: url("../../assets/images/icon_yijian.png");
}
#mine .menuList .phone a:before {
  background-image: url("../../assets/images/icon_phone.png");
}


