
#idea .ideaWrap {
  margin-top: 60px;
  border: 1px solid #ddd;
  border-left: none;
  border-right: none;
  background-color: #fff;
}
#idea .ideaWrap .ideaInner {
  display: block;
  width: 96%;
  height: 190px;
  margin: 8px 2%;
  border: none;
  font-size: 16px;
  resize: none;
}
#idea .sub {
  display: block;
  width: 96%;
  height: 45px;
  color: #fff;
  background-color: #dab96e;
  border: none;
  margin: 25px 2%;
  font-size: 16px;
  letter-spacing: 3px;
}


