
.carTop .CarInfo {
  width: 100%;
  height: 90px;
  background-color: #fff;
  position: relative;
  border-bottom: 1px solid #ddd;
/*margin-bottom:10px*/
}
.carTop .CarInfo:before {
  width: 30px;
  height: 90px;
  position: absolute;
  right: 0;
  text-align: center;
  line-height: 90px;
  color: #999;
}
.carTop .CarInfo .CarInfoWrap {
  padding-left: 70px;
  overflow: hidden;
}
.carTop .CarInfo .CarInfoWrap .carLogo {
  position: absolute;
  top: 15px;
  left: 0;
  width: 40px;
  height: 40px;
  padding: 10px;
  float: left;
}
.carTop .CarInfo .CarInfoWrap .carLogo img {
  margin: 0 auto;
  max-width: 100%;
}
.carTop .CarInfo .CarInfoWrap .carInfo {
  float: left;
  width: 100%;
  font-family: "";
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID {
  margin: 15px 0;
  width: 105px;
  height: 32px;
  background-image: url("../assets/images/bg_carID.png");
  background-size: 100% 100%;
  font-family: "";
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  line-height: 32px;
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID span {
  display: block;
  width: 80px;
  position: absolute;
  top: 0;
  right: -80px;
  color: #999;
  letter-spacing: 0;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carDesc {
  font-family: "";
  color: #999;
  font-size: 14px;
  letter-spacing: 1px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 30px;
}
.numBar span {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #ddd;
  float: left;
  background-color: #f5f5f5;
}
.numBar span:before {
  font-weight: bold;
}
.numBar .init {
  border-left: none;
  border-right: none;
  width: 45px;
}
.numBar .init input {
  border: none;
  width: 41px;
  padding-left: 3px;
  text-align: center;
}
#cartCompile .cartCompileWrap {
  padding-top: 45px;
  padding-bottom: 110px;
}
#cartCompile .cartCompileWrap .content {
  margin-top: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#cartCompile .cartCompileWrap .content .tt {
/*line-height:50px*/
  line-height: 25px;
  padding: 15px 0;
  padding-left: 12px;
  color: #666;
  border-bottom: 1px solid #ddd;
  font-size: 18px;
  margin-bottom: -1px;
}
#cartCompile .cartCompileWrap .content .tt i {
  margin-right: 5px;
  display: inline-block;
  padding: 0;
}
#cartCompile .cartCompileWrap .content .tt i:before {
  color: #999;
  font-size: 24px;
  vertical-align: middle;
}
#cartCompile .cartCompileWrap .content .tt .sel:before {
  color: #c7a770;
}
#cartCompile .cartCompileWrap .content .item {
  margin-left: 42px;
  overflow: hidden;
  padding-bottom: 8px;
  border-bottom: 1px solid #ddd;
}
#cartCompile .cartCompileWrap .content .item:last-child {
  border: none;
}
#cartCompile .cartCompileWrap .content .item p {
  line-height: 58px;
/*float:left*/
}
#cartCompile .cartCompileWrap .content .item .ttl {
  width: 100%;
  display: block;
}
#cartCompile .cartCompileWrap .content .item .ttl .init {
  height: 45px;
}
#cartCompile .cartCompileWrap .content .item .ttl span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow: hidden;
/*&:last-child
                            line-height:20px
                            color:#999
                            height:20px
                            font-size:14px*/
}
#cartCompile .cartCompileWrap .content .item .ttl span:first-child {
  margin-top: 2px;
  line-height: 30px;
  height: 30px;
}
#cartCompile .cartCompileWrap .content .item .pj {
  width: 100%;
  display: block;
  min-height: 36px;
  line-height: 36px;
  overflow: hidden;
}
#cartCompile .cartCompileWrap .content .item .pj .desc {
  font-size: 14px;
  color: #666;
}
#cartCompile .cartCompileWrap .content .item .change {
  float: right;
  color: #c7a770;
  overflow: hidden;
  margin: 0 10px 0 0;
}
#cartCompile .cartCompileWrap .content .item .change .init {
  background-color: #fff;
}
#cartCompile .cartCompileWrap .content .item .change .init input {
  background: none;
}
#cartCompile .totalWrap {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 50px;
  left: 0;
  background-color: #f5f5f5;
  border-top: 1px solid #ddd;
}
#cartCompile .totalWrap .left {
  float: left;
  line-height: 50px;
  padding-left: 12px;
  color: #333;
}
#cartCompile .totalWrap .left span {
  font-size: 24px;
  vertical-align: middle;
  color: #999;
  margin-right: 5px;
}
#cartCompile .totalWrap .left .sel {
  color: #c7a770;
}
#cartCompile .totalWrap .right {
  height: 50px;
  float: right;
  position: relative;
  padding-right: 107px;
}
#cartCompile .totalWrap .right .buy {
  position: absolute;
  top: 7px;
  right: 12px;
  display: inline-block;
  width: 90px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background-color: #c7a770;
  color: #fff;
}


