
#insurance .insuranceWrap {
  margin-top: 45px;
}
#insurance .insuranceWrap .showimg img {
  width: 100%;
}
#insurance .insuranceWrap .info {
  margin-top: 15px;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#insurance .insuranceWrap .info div {
  height: 45px;
  line-height: 45px;
  margin-left: 10px;
  border-bottom: 1px solid #ddd;
  position: relative;
}
#insurance .insuranceWrap .info div:last-child {
  border-bottom: none;
}
#insurance .insuranceWrap .info div:before {
  position: absolute;
  right: 10px;
  color: #999;
}
#insurance .insuranceWrap .info div label {
  display: inline-block;
  width: 33%;
  color: #999;
}
#insurance .insuranceWrap .info div input {
  display: inline-block;
  width: 60%;
  height: 44px;
}
#insurance .insuranceWrap .btnWrap {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  z-index: 2;
}
#insurance .insuranceWrap .btnWrap .charge {
  width: 94%;
  margin: 20px 3% 10px 3%;
  background-color: #c7a770;
  height: 44px;
  line-height: 44px;
  color: #fff;
  border: none;
  font-size: 18px;
}
#insurance .insuranceWrap .sex-picker {
  width: 100%;
  height: 100%;
  position: fixed;
  bottom: 0;
  z-index: 5;
}
#insurance .insuranceWrap .sex-picker .mask {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.5);
}
#insurance .insuranceWrap .sex-picker .pickWrap {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
}
#insurance .insuranceWrap .sex-picker .pickWrap .opwrap {
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
#insurance .insuranceWrap .sex-picker .pickWrap .opwrap span {
  display: block;
  float: left;
  width: 50%;
  color: #26a2ff;
  height: 40px;
  line-height: 40px;
  text-align: center;
}


