
#share {
  margin-bottom: 60px;
}
#share .shareWrap {
  padding-top: 45px;
}
#share .shareWrap .code {
  height: 210px;
  width: 100%;
  color: #666;
  text-align: center;
  line-height: 50px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#share .shareWrap .code .codeibj {
  display: block;
  height: 150px;
  width: 150px;
  margin: 0 auto;
}
#share .listWrap {
/*padding-left:10px*/
  border-bottom: 1px solid #ddd;
}
#share .listWrap .tt {
  height: 30px;
  line-height: 30px;
  background-color: #eee;
  font-size: 15px;
  padding-left: 10px;
}
#share .listWrap .slide {
/*padding-left:10px*/
/*padding-left:10px*/
  position: absolute;
  top: 287px;
  bottom: 0px;
  width: 100%;
  overflow: hidden;
}
#share .listWrap .slide .list {
  padding-left: 10px;
  overflow: hidden;
  height: 60px;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  position: relative;
}
#share .listWrap .slide .list:last-child {
  border: none;
}
#share .listWrap .slide .list .showMob {
  position: absolute;
  right: 10px;
  top: 15px;
  font-size: 14px;
  padding: 6px 10px;
  background: #f0bb49;
  border-radius: 8px;
  color: #fff;
}
#share .listWrap .slide .list img {
  width: 40px;
  height: 40px;
  float: left;
  border-radius: 100%;
  margin-top: 10px;
}
#share .listWrap .slide .list .info p {
  margin-left: 60px;
}
#share .listWrap .slide .list .info p:first-child {
  color: #333;
  font-size: 17px;
  line-height: 35px;
  height: 35px;
}
#share .listWrap .slide .list .info p:last-child {
  color: #999;
  font-size: 14px;
  line-height: 15px;
}


