
.changePassword .changePasswordWrap {
  margin-top: 55px;
  border-top: 1px solid #ccc;
}
.changePassword .changePasswordWrap .regFrom {
  width: 100%;
}
.changePassword .changePasswordWrap .regFrom .ww {
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
.changePassword .changePasswordWrap .regFrom .ww div {
  height: 45px;
  line-height: 45px;
  margin-left: 45px;
  border-top: 1px solid #ccc;
  position: relative;
}
.changePassword .changePasswordWrap .regFrom .ww div:before {
  content: '';
  display: block;
  position: absolute;
  left: -40px;
  top: 5px;
  width: 35px;
  height: 35px;
  background-image: url("../../assets/images/icon_account.png");
  background-repeat: no-repeat;
  background-size: 19px 213px;
}
.changePassword .changePasswordWrap .regFrom .ww div:first-child {
  border-top: none;
}
.changePassword .changePasswordWrap .regFrom .ww div:first-child:before {
  background-position: 8px -135px;
}
.changePassword .changePasswordWrap .regFrom .ww div:nth-child(2):before {
  background-position: 8px -135px;
}
.changePassword .changePasswordWrap .regFrom .ww div:nth-child(3):before {
  background-position: 8px -135px;
}
.changePassword .changePasswordWrap .regFrom .ww div input {
  height: 45px;
  width: 100%;
}
.changePassword .changePasswordWrap .regFrom .sub {
  width: 94%;
  height: 45px;
  background-color: #c7a770;
  color: #fff;
  display: block;
  margin: 15px auto;
}


