
.articleDetail {
  position: fixed;
  top: 0;
  left: 0;
  padding: 10px 2%;
  background-color: #fff;
  height: 100%;
  width: 96%;
  overflow-y: auto;
}
.articleDetail img {
  max-width: 100%;
}
.articleDetail .tt {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #000;
  overflow: hidden;
}
.articleDetail .tm {
  font-size: 15px;
  color: #999;
}
.articleDetail .desc2 {
  margin-top: 20px;
  padding-left: 0;
/*height:50px*/
  line-height: 25px;
  color: #999;
  overflow: hidden;
}
.articleDetail .desc2 p {
  text-indent: 2rem;
  word-break: break-all;
  word-wrap: break-word;
}


