
@import '../../assets/font/iconfont.css';
.headerCart {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 45px;
  background: #373737;
  color: #fff;
  font-size: 20px;
  line-height: 45px;
}
.headerCart .currentArea {
  float: left;
  padding-left: 8px;
  font-size: 13px;
  position: absolute;
}
.headerCart .currentArea .iconfont {
  display: inline-block;
  color: #dab96e;
  width: 45px;
  height: 45px;
}
.headerCart .currentArea .iconfont:before {
  font-size: 20px;
  padding: 0 5px;
}
.headerCart .title {
  width: 100%;
  text-align: center;
  color: #dab96e;
}
.headerCart .headRight {
  position: absolute;
  right: 15px;
  top: 0;
  min-width: 80px;
  background: url("../../assets/images/icon_serve_10.png") no-repeat;
  background-size: 22px 22px;
  background-position: right center;
}
.headerCart .headRight a {
  display: block;
  height: 45px;
  font-size: 16px;
  text-align: right;
  color: #dab96e;
}
.headerCart .headRight a span {
  display: block;
  float: right;
  margin-right: -7px;
  margin-top: 7px;
  width: 18px;
  height: 12px;
  line-height: 12px;
  text-align: center;
  font-size: 12px;
  color: #fff;
  border-radius: 5px;
  background-color: #dab96e;
}
.headerCart .headRight .search {
  text-align: right;
}
.headerCart .headRight .msgTip {
  text-align: center;
}


