
@import '../../assets/font/iconfont.css';
.headerCommon {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 45px;
  background: #373737;
  color: #fff;
  font-size: 20px;
  line-height: 45px;
}
.headerCommon .currentArea {
  float: left;
  padding-left: 0px !important;
  text-align: center;
  font-size: 13px;
  position: absolute;
}
.headerCommon .currentArea .iconfont {
  display: inline-block;
  color: #dab96e;
  width: 45px;
  height: 45px;
}
.headerCommon .currentArea .iconfont:before {
  font-size: 20px;
  padding: 0 5px;
}
.headerCommon .title {
  width: 100%;
  text-align: center;
  color: #dab96e;
}
.headerCommon .headRight {
  position: absolute;
  right: 0;
  top: 0;
  min-width: 80px;
}
.headerCommon .headRight a {
  display: block;
  padding-right: 10px;
  font-size: 16px;
  text-align: right;
  color: #dab96e;
}
.headerCommon .headRight span {
  display: inline-block;
  width: 35px;
  font-size: 22px;
}
.headerCommon .headRight .search {
  text-align: right;
}
.headerCommon .headRight .msgTip {
  text-align: center;
}


