
#roadHelp .roadHelpWrap {
  margin-top: 45px;
}
#roadHelp .roadHelpWrap .mint-navbar {
  border-bottom: 1px solid #ddd;
  position: fixed;
  z-index: 1;
  top: 45px;
  left: 0;
  width: 100%;
}
#roadHelp .roadHelpWrap .mint-navbar .mint-tab-item-label {
  font-size: 16px;
  color: #666;
}
#roadHelp .roadHelpWrap .mint-tab-item.is-selected {
  border-color: #c7a770;
}
#roadHelp .roadHelpWrap .mint-tab-item.is-selected .mint-tab-item-label {
  color: #c7a770;
}
#roadHelp .roadHelpWrap .mint-tab-container .list {
  padding-top: 55px;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item {
/*margin-top:10px*/
  background-color: #fff;
/*padding-bottom:10px*/
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .top {
  height: 80px;
  position: relative;
  padding-left: 120px;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .top img {
  position: absolute;
  left: 10px;
  top: 14px;
  display: block;
  border: 1px solid #ddd;
  width: 100px;
  height: 50px;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .top p {
  width: 100%;
  overflow: hidden;
  padding-top: 18px;
  color: #546273;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .top p span {
  display: block;
  font-size: 18px;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .top p span:last-child {
  padding-top: 4px;
  font-size: 16PX;
  color: #9e9e9e;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .bottom ul {
  overflow: hidden;
  margin-left: 10px;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .bottom ul li {
  display: block;
  float: left;
  width: 50%;
  color: #546273;
  line-height: 24px;
  overflow: hidden;
}
#roadHelp .roadHelpWrap .mint-tab-container .list .slideBar .item .bottom ul li span {
  margin-right: 6px;
}
#roadHelp .roadHelpWrap .call {
  width: 94%;
  display: block;
  margin: 10px auto;
  background-color: #c7a770;
  border: none;
  font-size: 18px;
  color: #fff;
  height: 44px;
  line-height: 44px;
}
#roadHelp .roadHelpWrap .call span {
  vertical-align: top;
  font-size: 18px;
  margin-right: 6px;
  display: inline-block;
  height: 44px;
  line-height: 40px;
}


