
#discount #discountWrap {
  margin-top: 98px;
}
#discount #discountWrap .mint-navbar {
  position: fixed;
  top: 45px;
  width: 100%;
  border-bottom: 1px solid #ddd;
}
#discount #discountWrap .mint-navbar .mint-tab-item-label {
  font-size: 16px;
  color: #666;
}
#discount #discountWrap .mint-tab-item.is-selected {
  border-color: #c7a770;
}
#discount #discountWrap .mint-tab-item.is-selected .mint-tab-item-label {
  color: #c7a770;
}
#discount #discountWrap .mint-tab-container {
  overflow: auto;
  position: static;
}
#discount #discountWrap .mint-tab-container .list {
  position: absolute;
  top: 97px;
  bottom: 10px;
  z-index: -1;
  width: 100%;
/*overflow:hidden*/
}
#discount #discountWrap .mint-tab-container .list .slideBar {
  padding-top: 10px;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item {
  margin: 0 10px 10px 10px;
/*height:102px*/
  position: relative;
  padding-left: 117px;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item div {
  height: 102px;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item .left {
  position: absolute;
  left: 0;
  top: 0;
  background-color: #fff;
  width: 100px;
  text-align: center;
  line-height: 100px;
  border: 1px solid #ddd;
  border-right: none;
  font-family: "";
  font-size: 13px;
  color: #c7a770;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item .left span {
  font-size: 26px;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item .center {
  position: absolute;
  left: 101px;
  top: 0;
  background: url("../../assets/images/bg_disc2.png") no-repeat;
  background-size: 17px 100px;
  width: 17px;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item .right {
  background-color: #fff;
  border: 1px solid #ddd;
  border-left: none;
  padding-left: 15px;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item .right h4 {
  margin-top: 15px;
  margin-bottom: 8px;
  font-size: 20px;
  color: #666;
}
#discount #discountWrap .mint-tab-container .list .slideBar .item .right p {
  color: #999;
  font-size: 14px;
  line-height: 20px;
}
#discount #discountWrap .mint-tab-container .dated .list .slideBar .item .left {
  color: #ccc;
}
#discount #discountWrap .mint-tab-container .dated .list .slideBar .item .left span {
  color: #ccc;
}
#discount #discountWrap .mint-tab-container .dated .list .slideBar .item .right h4 {
  color: #ccc;
}
#discount #discountWrap .mint-tab-container .dated .list .slideBar .item .right p {
  color: #ccc;
}


