
#information .informationWrap {
  margin-top: 45px;
}
#information .informationWrap .head {
  height: 100px;
  background-color: #fff;
  padding-top: 20px;
  border-bottom: 1px solid #ddd;
}
#information .informationWrap .head .headWrap {
  width: 80px;
  height: 80px;
  position: relative;
  left: 50%;
  margin-left: -40px;
  border-radius: 100%;
  -webkit-mask-image: url("../../assets/images/header_default.png");
  -webkit-mask-size: 100% 100%;
}
#information .informationWrap .head .headWrap form {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  height: 100%;
  opacity: 0;
}
#information .informationWrap .head .headWrap form input {
  height: 100%;
}
#information .informationWrap .head .headWrap img {
  width: 80px;
  height: 80px;
/*border-radius:100%*/
  display: block;
}
#information .informationWrap .head .headWrap p {
  position: absolute;
  bottom: 0;
  text-align: center;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
  width: 100%;
  height: 30px;
  line-height: 25px;
  font-size: 12px;
}
#information .informationWrap .info {
  margin-top: 15px;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#information .informationWrap .info div {
  height: 45px;
  line-height: 45px;
  margin-left: 10px;
  border-bottom: 1px solid #ddd;
  position: relative;
}
#information .informationWrap .info div:last-child {
  border-bottom: none;
}
#information .informationWrap .info div:before {
  position: absolute;
  right: 10px;
  color: #999;
}
#information .informationWrap .info div label {
  display: inline-block;
  width: 33%;
  color: #999;
  float: left;
}
#information .informationWrap .info div input {
  display: inline-block;
  width: 60%;
  height: 44px;
  background-color: #fff;
  float: left;
}
#information .informationWrap .info .dis input {
  color: #999;
}
#information .informationWrap .code {
  height: 250px;
  width: 100%;
  color: #666;
  text-align: center;
  line-height: 50px;
  background-color: #fff;
  padding-top: 30px;
}
#information .informationWrap .code img {
  display: block;
  height: 190px;
  width: 190px;
  margin: 0 auto;
}
#information .sex-picker {
  width: 100%;
  height: 100%;
  position: fixed;
  bottom: 0;
}
#information .sex-picker .mask {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.5);
}
#information .sex-picker .pickWrap {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
}
#information .sex-picker .pickWrap .opwrap {
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
#information .sex-picker .pickWrap .opwrap span {
  display: block;
  float: left;
  width: 50%;
  color: #26a2ff;
  height: 40px;
  line-height: 40px;
  text-align: center;
}


