
#report #reportWrap {
  margin-top: 45px;
}
#report #reportWrap .list {
  padding-left: 85px;
  padding-top: 8px;
  border-bottom: 1px solid #ddd;
  min-height: 82px;
  background: url("../../assets/images/icon_report.png") no-repeat;
  background-color: #fff;
  background-size: 50px 50px;
  background-position: 20px 10px;
}
#report #reportWrap .list p {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 25px;
  color: #999;
}
#report #reportWrap .list p:first-child {
  color: #333;
}
#report #reportWrap .list p span {
  color: #c7a770;
}
#report .sp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}
#report .sp .mask {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
}
#report .sp .video {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  margin-top: 45px;
  width: 100%;
}
#report iframe {
  width: 100%;
/*height:0*/
}


