
.login .registerWrap {
  margin-top: 55px;
  border-top: 1px solid #ccc;
}
.login .registerWrap .regFrom {
/*background-color:#fff*/
  width: 100%;
}
.login .registerWrap .regFrom div {
  height: 45px;
  line-height: 45px;
  margin-left: 45px;
  border-top: 1px solid #ccc;
  position: relative;
}
.login .registerWrap .regFrom div:before {
  content: '';
  display: block;
  position: absolute;
  left: -45px;
  top: 0px;
  width: 45px;
  height: 45px;
  background-image: url("../assets/images/icon_account.png");
  background-repeat: no-repeat;
  background-size: 19px 213px;
  background-color: #fff;
}
.login .registerWrap .regFrom div:first-child {
  border-top: none;
}
.login .registerWrap .regFrom div:first-child:before {
  background-position: 13px -34px;
  border-bottom: 1px solid #fff;
}
.login .registerWrap .regFrom div:nth-child(2):before {
  background-position: 13px -179px;
}
.login .registerWrap .regFrom div:nth-child(3) input {
  width: 60%;
}
.login .registerWrap .regFrom div:nth-child(3) img {
  display: inline-block;
  width: 70px;
  height: 30px;
  vertical-align: middle;
  border: none;
}
.login .registerWrap .regFrom div:nth-child(3):before {
  background-position: 13px -82px;
}
.login .registerWrap .regFrom div:last-child {
  border-top: none;
  text-align: center;
  margin: 0;
}
.login .registerWrap .regFrom div .getCode {
  height: 25px;
  width: 90px;
  line-height: 25px;
  color: #fff;
  background-color: #c7a770;
  float: right;
  margin-top: 10px;
  margin-right: 20px;
}
.login .registerWrap .regFrom div .checkBox {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 1px solid #bbb;
  line-height: 19px;
  margin: 0;
}
.login .registerWrap .regFrom div .checked {
  color: #c7a770;
}
.login .registerWrap .regFrom div input {
  height: 45px;
  width: 100%;
}
.login .registerWrap .regFrom div .code {
  width: 60%;
}
.login .registerWrap .regFrom div .router-link-active {
  color: #c7a770;
}
.login .registerWrap .regFrom .bt {
  height: 1px;
  border-top: 1px solid #ccc;
  margin: 0;
}
.login .registerWrap .regFrom .sub {
  width: 94%;
  height: 45px;
  background-color: #c7a770;
  color: #fff;
  display: block;
  margin: 15px auto;
}
.login .registerWrap .loginOperate {
  margin-left: 0 !important;
  border-top: none !important;
  position: initial;
  overflow: hidden;
}
.login .registerWrap .loginOperate:before {
  display: none !important;
}
.login .registerWrap .loginOperate p {
  width: 45%;
  float: left;
}
.login .registerWrap .loginOperate p:first-child {
  margin-left: 5%;
}
.login .registerWrap .loginOperate p:last-child {
  text-align: right;
}
.login .registerWrap .loginOperate p span {
  font-size: 24px;
  vertical-align: middle;
  margin-right: 5px;
  color: #c3c3c3;
}
.login .registerWrap .loginOperate p .sel {
  color: #dab96e;
}


