
.carTop .CarInfo {
  width: 100%;
  height: 90px;
  background-color: #fff;
  position: relative;
  border-bottom: 1px solid #ddd;
/*margin-bottom:10px*/
}
.carTop .CarInfo:before {
  width: 30px;
  height: 90px;
  position: absolute;
  right: 0;
  text-align: center;
  line-height: 90px;
  color: #999;
}
.carTop .CarInfo .CarInfoWrap {
  padding-left: 70px;
  overflow: hidden;
}
.carTop .CarInfo .CarInfoWrap .carLogo {
  position: absolute;
  top: 15px;
  left: 0;
  width: 40px;
  height: 40px;
  padding: 10px;
  float: left;
}
.carTop .CarInfo .CarInfoWrap .carLogo img {
  margin: 0 auto;
  max-width: 100%;
}
.carTop .CarInfo .CarInfoWrap .carInfo {
  float: left;
  width: 100%;
  font-family: "";
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID {
  margin: 15px 0;
  width: 105px;
  height: 32px;
  background-image: url("../assets/images/bg_carID.png");
  background-size: 100% 100%;
  font-family: "";
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  line-height: 32px;
  position: relative;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carID span {
  display: block;
  width: 80px;
  position: absolute;
  top: 0;
  right: -80px;
  color: #999;
  letter-spacing: 0;
}
.carTop .CarInfo .CarInfoWrap .carInfo .carDesc {
  font-family: "";
  color: #999;
  font-size: 14px;
  letter-spacing: 1px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 30px;
}
.numBar span {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #ddd;
  float: left;
  background-color: #f5f5f5;
}
.numBar span:before {
  font-weight: bold;
}
.numBar .init {
  border-left: none;
  border-right: none;
  width: 45px;
}
.numBar .init input {
  border: none;
  width: 41px;
  padding-left: 3px;
  text-align: center;
}
#cartAccessories .cartAccessoriesWrap {
  padding-top: 45px;
  padding-bottom: 50px;
}
#cartAccessories .cartAccessoriesWrap .content {
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
#cartAccessories .cartAccessoriesWrap .content .item {
  overflow: hidden;
  height: 50px;
  margin-left: 12px;
  padding-right: 12px;
  border-bottom: 1px solid #ddd;
}
#cartAccessories .cartAccessoriesWrap .content .item:last-child {
  border: none;
}
#cartAccessories .cartAccessoriesWrap .content .item .ttl {
  float: left;
  width: 50%;
  height: 100%;
  display: table;
}
#cartAccessories .cartAccessoriesWrap .content .item .ttl span {
  display: table-cell;
  vertical-align: middle;
}
#cartAccessories .cartAccessoriesWrap .content .item .change {
  float: right;
  margin-top: 10px;
}
#cartAccessories .cartAccessoriesWrap .content .item .init input {
  height: 30px;
  line-height: 30px;
}
#cartAccessories .cartAccessoriesWrap .content .item .select {
  width: 30px;
  float: left;
  height: 50px;
  line-height: 50px;
  font-size: 24px;
  color: #999;
}
#cartAccessories .cartAccessoriesWrap .content .item .sel:before {
  color: #c7a770;
}


