
@import '../../assets/font/iconfont.css';
.headerIndex {
  position: fixed;
  z-index: 199;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 45px;
  background: #373737;
  color: #fff;
  line-height: 45px;
}
.headerIndex .wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.headerIndex .wrap .currentArea {
  float: left;
  padding-left: 8px;
  font-size: 13px;
  position: absolute;
}
.headerIndex .wrap .icon-chevron-copy-copy-copy a {
  color: #fff;
}
.headerIndex .wrap .icon-chevron-copy-copy-copy:before {
  float: right;
  font-size: 12px;
  padding: 0 5px;
}
.headerIndex .wrap .title {
  width: 100%;
  text-align: center;
  font-size: 20px;
}
.headerIndex .wrap .headRight {
  position: absolute;
  right: 0;
  top: 0;
  min-width: 80px;
}
.headerIndex .wrap .headRight span {
  display: inline-block;
  width: 35px;
  font-size: 22px;
}
.headerIndex .wrap .headRight .search {
  text-align: right;
}
.headerIndex .wrap .headRight .msgTip {
  text-align: center;
}
.headerIndex .wrap2 {
/*display:none*/
  position: absolute;
  color: #c7a76f;
  opacity: 0;
}
.headerIndex .wrap2 .currentArea .posi {
  color: #c7a76f;
}


