
.register .registerWrap {
  margin-top: 55px;
  border-top: 1px solid #ccc;
}
.register .registerWrap .regFrom {
/*background-color:#fff*/
  width: 100%;
}
.register .registerWrap .regFrom div {
  height: 45px;
  line-height: 45px;
  margin-left: 45px;
  border-top: 1px solid #ccc;
  position: relative;
  background-color: #fff;
}
.register .registerWrap .regFrom div:before {
  content: '';
  display: block;
  position: absolute;
  left: -45px;
  top: 0px;
  width: 45px;
  height: 45px;
  background-image: url("../assets/images/icon_account.png");
  background-repeat: no-repeat;
  background-size: 19px 213px;
  border-bottom: 1px solid #fff;
  background-color: #fff;
}
.register .registerWrap .regFrom div:first-child {
  border-top: none;
}
.register .registerWrap .regFrom div:first-child:before {
  background-position: 13px 13px;
}
.register .registerWrap .regFrom div:nth-child(2):before {
  background-position: 13px -34px;
}
.register .registerWrap .regFrom div:nth-child(3):before {
  background-position: 13px -82px;
}
.register .registerWrap .regFrom div:nth-child(4):before {
  background-position: 13px -130px;
}
.register .registerWrap .regFrom div:nth-child(5):before {
  background-position: 13px -179px;
}
.register .registerWrap .regFrom div:last-child {
  border-top: none;
  text-align: center;
  margin: 0;
  border-bottom: none;
}
.register .registerWrap .regFrom div .getCode {
  height: 25px;
  width: 90px;
  line-height: 25px;
  color: #fff;
  background-color: #c7a770;
  float: right;
  margin-top: 10px;
  margin-right: 20px;
  font-size: 13px;
}
.register .registerWrap .regFrom div .disabled {
  background-color: #b9b9b9;
}
.register .registerWrap .regFrom div .checkBox {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 1px solid #bbb;
  line-height: 19px;
  margin: 0;
  color: #f5f5f5;
}
.register .registerWrap .regFrom div .checked {
  color: #c7a770;
}
.register .registerWrap .regFrom div input {
  height: 45px;
  width: 100%;
}
.register .registerWrap .regFrom div .code {
  width: 60%;
}
.register .registerWrap .regFrom .bt {
  height: 1px;
  border-top: 1px solid #ccc;
  margin: 0;
}
.register .registerWrap .regFrom .sub {
  width: 94%;
  height: 45px;
  background-color: #c7a770;
  color: #fff;
  display: block;
  margin: 15px auto;
}


