  margin: 5px auto;
  background-image: url("../assets/images/nav.png");
  background-size: 330px 130px;
}
.index-wrap .nav-wrap a:first-child:before {
  background-position: 0 0;
}
.index-wrap .nav-wrap a:nth-child(2):before {
  background-position: -69px 0;
}
.index-wrap .nav-wrap a:nth-child(3):before {
