}
.index-wrap .nav-wrap a:nth-child(7):before {
  background-position: -69px -78px;
}
.index-wrap .nav-wrap a:nth-child(8):before {
  background-position: -140px -78px;
}
.index-wrap .nav-wrap a:nth-child(9):before {
  background-position: -210px -78px;
}
.index-wrap .nav-wrap a:nth-child(10):before {
